.main-r-c{
    background-image: url("https://sevdesk.imgix.net/cta-generic-2.jpg?fit=crop&h=384&w=1280&auto=format,compress&q=50");
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 100px;
}
.main-r-c h2, .main-r-c p{
    color: rgb(170, 170, 170);
    text-align: center;
}
.form-outline{
    background-color: white;
    width: 600px;
    height: 50px;
    margin-left: 10px;
    margin-right: 10px;
    line-height: 2.5;
}
.input-group{
  display: flex;
  justify-content: center;
}
button.register-btn {
    position: relative;
    right: 10px;
    width: 190px;
    height: 50px;
    line-height: 52px;
    background: 0;
    font-size: 16px;
    border: 0;
    color: white;
    font-weight: bold;
  }
  button.register-btn:hover:after {
    top: -2px;
    box-shadow: 0px 5px 10px -4px rgba(0, 0, 0, 0.3);
    cursor: pointer;
  }
  button.register-btn:after {
    content: "Get Started";
    display: block;
    position: absolute;
    border-radius: 3px;
    width: 60%;
    height: 100%;
    transition: all 0.2s;
    box-shadow: 0px 5px 10px -4px rgba(0, 0, 0, 0.1);
    top: 0;
    left: 0;
  }
  button.register-btn:after {
    background: #4584e2;
}
@media scren and (max-width:812px){
button.register-btn {
    position: relative;
    right: 0px;
    left: 20px;
    width: 190px;
    height: 50px;
    line-height: 52px;
    background: 0;
    font-size: 16px;
    border: 0;
    color: white;
    font-weight: bold;
  }
}
@media scren and (max-width:635px){
