@import url('https://fonts.googleapis.com/css2?family=Goblin+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
.accounting-div{
    background-image:url('https://sevdesk.imgix.net/34060/1610111446-logiciel-de-facturation.jpg?fp-z=1&fp-y=.5&fp-x=.6&fit=crop&auto=format,compress&q=60&blend-mode=color&blend-color=173E5E&con=-21&exp=-15');
    width: 100%;
    height: 460px;
    background-repeat: no-repeat;
    background-size:cover ;
  }
  .accounting-div h1{
    padding-top:110px;
    padding-left: 155px;
    font-size: 39px;
    color: white;
    font-family: 'Libre Franklin', sans-serif;
      }
      .accounting-div p{
        padding-left: 155px;
        padding-top: 30px;
        color: white;
        font-size: 20px;
        font-family: cursive;
      }
      button.blu {
        margin: 0 6px;
        position: relative;
        top: 7.5%;
        left: 11%;
        width: 190px;
        height: 38px;
        line-height: 38px;
        background: 0;
        font-size: 16px;
        border: 0;
        color: white;
        font-weight: bold;
      }
      button.blu:hover:after {
        top: -2px;
        box-shadow: 0px 5px 10px -4px rgba(0, 0, 0, 0.3);
        cursor: pointer;
      }
      button.blu:after {
        content: "REGISTER FOR FREE";
        display: block;
        position: absolute;
        border-radius: 3px;
        width: 100%;
        height: 100%;
        transition: all 0.2s;
        box-shadow: 0px 5px 10px -4px rgba(0, 0, 0, 0.1);
        top: 0;
        left: 0;
      }
      button.blu:after {
        background: #4584e2;
    }
      /* button.registerbtn:after {
        background: #4482dc;
      } */
      .accounting-div p br{
        display: none;
      }
      @media screen and (max-width: 1300px) {
        .accounting-div h1{
          padding-left: 140px;
        }
        .accounting-div p{
          padding-left: 140px;
        }
        button.blu{
          left: 10%;
        }
      
        @media screen  and (max-width: 1200px) {
          .accounting-div h1{
            padding-left: 130px;
          }
          .accounting-div p{
            padding-left: 130px;
          }
          button.blu{
            left: 9%;
          }       
        }
      }
      @media screen and (max-width: 992px) {
        .accounting-div h1{
          padding-left: 60px;
        }
        .accounting-div p br{
          display: block;
        }
        .accounting-div p{
          padding-left: 60px;
        }
        button.blu{
          left: 5%;
        }       
      }

      @media  screen and (max-width: 767.98px) {
        .accounting-div{
          background-image: none;
          background-color: #173e5e;
        }
        .accounting-div h1{
          padding-left: 60px;
          font-size: 20px;
          color: white;
              font-family: 'Poppins', sans-serif;
        
            }
            .accounting-div p{
              padding-left: 60px;
              padding-top: 30px;
              color: white;
              font-size: 30px;
          font-family: cursive;
          font-weight: 700;

                        }
            .accounting-div h1 br{
              display: none;
            }
            button.blu {
              margin: 0 6px;
              position: relative;
              top: 7.5%;
              left: 8%;
              width: 400px;
              height: 38px;
              line-height: 38px;
              background: 0;
              font-size: 12px;
              border: 0;
              color: white;
              font-weight: bold;
            }
      }
      @media  screen and (max-width: 575px) {
        .accounting-div{
          display: flex;
          flex-direction: column;
          align-items: center;
        }
        .accounting-div h1, .accounting-div p{
          padding-left: 0%;
        }
        .accounting-div p{
          padding-top: 10px;
          font-size: 25px;
        }
        .s{
          visibility: hidden;
        }
