*{
    margin: 0;
    padding: 0;
}
header ul a {
display: flex;
line-height: 3;
display: inline-block;
justify-content: space-around;
margin-inline: 10px;
text-decoration: none;
font-family: Arial, Helvetica, sans-serif;
}
#header-btn{
    font-size: 12px;
    max-height: 30px;
    display: flex;
    align-items: center;
    margin-top: 10px;
    border-radius: none;
}

header ul{
    display: flex;
    justify-content: flex-end;
}
header{
background-color: white;
height: 70px;
max-width: 100%;
}
header ul img {
    position: absolute;
