        height: 33px;
    width: 35px;
    font-weight: 500;
    font-size: 27px;
}
button.blue {
    margin-top: 15px;
    position: relative;
    top: 50%;
    width: 150px;
    height: 45px;
    background: 0;
    color: white;
    line-height: 2.5;
    font-size: 19px;
    font-weight: 500;
    left:-170px;
    border: none;
  
  }
  button.blue:hover:after {
    top: -1px;
    box-shadow: 0px 5px 10px -4px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    zoom: 105%;
  }
  button.blue:after {
    content: "GET STARTED";
    display: block;
    position: absolute;
    border-radius: 3px;
    width: 100%;
    height: 100%;
    transition: all 0.2s;
    box-shadow: 0px 5px 10px -4px rgba(0, 0, 0, 0.1);
    top: 0;
    left: 0;
  }
  button.blue:after {
    background: #4482dc;
  }
  a.login {
    position: relative;
    right: 180px;
    text-decoration: none;
    color: black;
    font-size: 18px;
    margin-top: 10px;
    font-family: 'Open Sans', sans-serif;
    font-weight: lighter;
}

  a.login:hover{
    color: #4482dc;
  }
  
  a.login:hover::after {
    content: "";
    position: absolute;
    left: 10%;
    right: 10%;
    bottom: 10px;  
    border: 1px solid black;
  }

